<template>
  <div class="task-management-container">
    <div class="page-header">
      <h1>任务管理</h1>
      <p>智能调度无人机作业任务，高效管理农业生产流程</p>
    </div>
    
    <div class="task-management-content">
      <!-- 功能工具栏 -->
      <div class="toolbar">
        <div class="search-filter">
          <input 
            type="text" 
            v-model="searchQuery"
            placeholder="搜索任务名称或农田..."
            class="search-input"
          />
          <select v-model="statusFilter" class="filter-select">
            <option value="">全部状态</option>
            <option value="待执行">待执行</option>
            <option value="执行中">执行中</option>
            <option value="已完成">已完成</option>
            <option value="已取消">已取消</option>
            <option value="失败">失败</option>
          </select>
          <select v-model="typeFilter" class="filter-select">
            <option value="">全部类型</option>
            <option value="作物监测">作物监测</option>
            <option value="农药喷洒">农药喷洒</option>
            <option value="播种作业">播种作业</option>
            <option value="施肥作业">施肥作业</option>
            <option value="灌溉监测">灌溉监测</option>
          </select>
        </div>
        <button class="btn-create-task" @click="showCreateTaskModal = true">
          <i class="fa fa-plus"></i> 创建新任务
        </button>
      </div>
      
      <!-- 任务统计卡片 -->
      <div class="stats-cards">
        <div class="stat-card">
          <div class="stat-icon total-icon"><i class="fa fa-tasks"></i></div>
          <div class="stat-content">
            <div class="stat-number">{{ tasks.length }}</div>
            <div class="stat-label">总任务数</div>
          </div>
        </div>
        <div class="stat-card">
          <div class="stat-icon pending-icon"><i class="fa fa-clock-o"></i></div>
          <div class="stat-content">
            <div class="stat-number">{{ pendingTasks }}</div>
            <div class="stat-label">待执行</div>
          </div>
        </div>
        <div class="stat-card">
          <div class="stat-icon active-icon"><i class="fa fa-play-circle"></i></div>
          <div class="stat-content">
            <div class="stat-number">{{ activeTasks }}</div>
            <div class="stat-label">执行中</div>
          </div>
        </div>
        <div class="stat-card">
          <div class="stat-icon completed-icon"><i class="fa fa-check-circle"></i></div>
          <div class="stat-content">
            <div class="stat-number">{{ completedTasks }}</div>
            <div class="stat-label">已完成</div>
          </div>
        </div>
      </div>
      
      <!-- 任务列表 -->
      <div class="task-list-section">
        <h2>任务列表</h2>
        <div class="task-table-container">
          <table class="task-table">
            <thead>
              <tr>
                <th>任务名称</th>
                <th>任务类型</th>
                <th>目标农田</th>
                <th>计划时间</th>
                <th>执行时间</th>
                <th>执行人</th>
                <th>状态</th>
                <th>操作</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="task in filteredTasks" :key="task.id" :class="{ 'task-urgent': task.urgent }">
                <td class="task-name">{{ task.name }}</td>
                <td>
                  <span class="task-type-tag" :class="getTaskTypeClass(task.type)">{{ task.type }}</span>
                </td>
                <td>{{ task.fieldName }}</td>
                <td>{{ task.scheduledTime }}</td>
                <td>{{ task.executionTime || '-' }}</td>
                <td>{{ task.executor || '自动' }}</td>
                <td>
                  <span class="task-status-badge" :class="getStatusClass(task.status)">{{ task.status }}</span>
                </td>
                <td>
                  <div class="task-actions">
                    <button class="btn-details" @click="viewTaskDetails(task)">
                      <i class="fa fa-eye"></i>
                    </button>
                    <button v-if="task.status === '待执行'" class="btn-edit" @click="editTask(task)">
                      <i class="fa fa-pencil"></i>
                    </button>
                    <button v-if="task.status === '待执行' || task.status === '执行中'" class="btn-cancel" @click="cancelTask(task.id)">
                      <i class="fa fa-times-circle"></i>
                    </button>
                    <button v-if="task.status === '待执行'" class="btn-start" @click="startTask(task.id)">
                      <i class="fa fa-play"></i>
                    </button>
                    <button v-if="task.status === '已完成' || task.status === '已取消' || task.status === '失败'" class="btn-delete" @click="deleteTask(task.id)">
                      <i class="fa fa-trash"></i>
                    </button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <div v-if="filteredTasks.length === 0" class="empty-state">
            <i class="fa fa-tasks"></i>
            <p>没有找到符合条件的任务</p>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 创建/编辑任务弹窗 -->
    <div class="modal" v-if="showCreateTaskModal || showEditTaskModal">
      <div class="modal-content">
        <div class="modal-header">
          <h3>{{ showCreateTaskModal ? '创建新任务' : '编辑任务' }}</h3>
          <button class="close-btn" @click="closeModal">&times;</button>
        </div>
        <div class="modal-body">
          <form @submit.prevent="saveTask">
            <div class="form-group">
              <label>任务名称 *</label>
              <input type="text" v-model="currentTask.name" required placeholder="请输入任务名称">
            </div>
            <div class="form-row">
              <div class="form-group">
                <label>任务类型 *</label>
                <select v-model="currentTask.type" required>
                  <option value="">请选择任务类型</option>
                  <option value="作物监测">作物监测</option>
                  <option value="农药喷洒">农药喷洒</option>
                  <option value="播种作业">播种作业</option>
                  <option value="施肥作业">施肥作业</option>
                  <option value="灌溉监测">灌溉监测</option>
                </select>
              </div>
              <div class="form-group">
                <label>目标农田 *</label>
                <select v-model="currentTask.fieldId" required @change="updateFieldName">
                  <option value="">请选择农田</option>
                  <option v-for="field in availableFields" :key="field.id" :value="field.id">{{ field.name }}</option>
                </select>
              </div>
            </div>
            <div class="form-row">
              <div class="form-group">
                <label>计划执行时间 *</label>
                <input type="datetime-local" v-model="currentTask.scheduledTime" required>
              </div>
              <div class="form-group">
                <label>执行人</label>
                <select v-model="currentTask.executor">
                  <option value="">自动分配</option>
                  <option value="操作员A">操作员A</option>
                  <option value="操作员B">操作员B</option>
                  <option value="操作员C">操作员C</option>
                </select>
              </div>
            </div>
            <div class="form-group">
              <label>任务参数</label>
              <div class="params-container">
                <div v-if="currentTask.type === '农药喷洒'" class="param-item">
                  <label>农药类型:</label>
                  <input type="text" v-model="currentTask.pesticideType" placeholder="请输入农药类型">
                </div>
                <div v-if="currentTask.type === '农药喷洒' || currentTask.type === '施肥作业'" class="param-item">
                  <label>用量(kg/亩):</label>
                  <input type="number" v-model.number="currentTask.usagePerAcre" min="0" step="0.1">
                </div>
                <div v-if="currentTask.type === '作物监测'" class="param-item">
                  <label>监测高度(m):</label>
                  <input type="number" v-model.number="currentTask.monitorHeight" min="1" step="1">
                </div>
                <div v-if="currentTask.type === '播种作业'" class="param-item">
                  <label>种子类型:</label>
                  <input type="text" v-model="currentTask.seedType" placeholder="请输入种子类型">
                </div>
                <div v-if="currentTask.type === '播种作业'" class="param-item">
                  <label>播种密度(株/亩):</label>
                  <input type="number" v-model.number="currentTask.seedDensity" min="1">
                </div>
              </div>
            </div>
            <div class="form-group">
              <label>任务描述</label>
              <textarea v-model="currentTask.description" rows="3" placeholder="请输入任务详细描述"></textarea>
            </div>
            <div class="form-group">
              <label class="checkbox-label">
                <input type="checkbox" v-model="currentTask.urgent"> 紧急任务
              </label>
            </div>
            <div class="form-actions">
              <button type="button" class="btn-cancel" @click="closeModal">取消</button>
              <button type="submit" class="btn-save">保存</button>
            </div>
          </form>
        </div>
      </div>
    </div>
    
    <!-- 任务详情弹窗 -->
    <div class="modal" v-if="showTaskDetails">
      <div class="modal-content large">
        <div class="modal-header">
          <h3>{{ selectedTask.name }} - 任务详情</h3>
          <button class="close-btn" @click="showTaskDetails = false">&times;</button>
        </div>
        <div class="modal-body">
          <div class="task-details-content">
            <div class="detail-section">
              <h4>基本信息</h4>
              <div class="detail-grid">
                <div class="detail-item">
                  <label>任务名称</label>
                  <span>{{ selectedTask.name }}</span>
                </div>
                <div class="detail-item">
                  <label>任务类型</label>
                  <span class="task-type-tag" :class="getTaskTypeClass(selectedTask.type)">{{ selectedTask.type }}</span>
                </div>
                <div class="detail-item">
                  <label>目标农田</label>
                  <span>{{ selectedTask.fieldName }}</span>
                </div>
                <div class="detail-item">
                  <label>计划时间</label>
                  <span>{{ selectedTask.scheduledTime }}</span>
                </div>
                <div class="detail-item">
                  <label>执行时间</label>
                  <span>{{ selectedTask.executionTime || '未执行' }}</span>
                </div>
                <div class="detail-item">
                  <label>执行人</label>
                  <span>{{ selectedTask.executor || '自动' }}</span>
                </div>
                <div class="detail-item">
                  <label>任务状态</label>
                  <span class="task-status-badge" :class="getStatusClass(selectedTask.status)">{{ selectedTask.status }}</span>
                </div>
                <div class="detail-item">
                  <label>创建时间</label>
                  <span>{{ selectedTask.createdAt }}</span>
                </div>
              </div>
              <div class="detail-item full-width">
                <label>任务描述</label>
                <p>{{ selectedTask.description || '无' }}</p>
              </div>
            </div>
            
            <div class="detail-section">
              <h4>任务参数</h4>
              <div class="task-params">
                <div v-if="selectedTask.pesticideType" class="param-detail">
                  <span class="param-label">农药类型:</span>
                  <span class="param-value">{{ selectedTask.pesticideType }}</span>
                </div>
                <div v-if="selectedTask.usagePerAcre" class="param-detail">
                  <span class="param-label">用量:</span>
                  <span class="param-value">{{ selectedTask.usagePerAcre }} kg/亩</span>
                </div>
                <div v-if="selectedTask.monitorHeight" class="param-detail">
                  <span class="param-label">监测高度:</span>
                  <span class="param-value">{{ selectedTask.monitorHeight }} m</span>
                </div>
                <div v-if="selectedTask.seedType" class="param-detail">
                  <span class="param-label">种子类型:</span>
                  <span class="param-value">{{ selectedTask.seedType }}</span>
                </div>
                <div v-if="selectedTask.seedDensity" class="param-detail">
                  <span class="param-label">播种密度:</span>
                  <span class="param-value">{{ selectedTask.seedDensity }} 株/亩</span>
                </div>
                <div v-if="!hasAnyParams()" class="no-params">
                  暂无特定参数
                </div>
              </div>
            </div>
            
            <div v-if="selectedTask.status === '已完成' || selectedTask.status === '执行中'" class="detail-section">
              <h4>执行详情</h4>
              <div class="execution-details">
                <div class="execution-item">
                  <div class="execution-label">执行无人机</div>
                  <div class="execution-value">{{ selectedTask.droneId || 'N/A' }}</div>
                </div>
                <div class="execution-item">
                  <div class="execution-label">执行时长</div>
                  <div class="execution-value">{{ selectedTask.executionDuration || '计算中...' }}</div>
                </div>
                <div class="execution-item">
                  <div class="execution-label">覆盖面积</div>
                  <div class="execution-value">{{ selectedTask.coveredArea || '0' }} 亩</div>
                </div>
                <div class="execution-item">
                  <div class="execution-label">任务进度</div>
                  <div class="execution-value">
                    <div class="progress-bar">
                      <div class="progress-fill" :style="{ width: selectedTask.progress + '%', backgroundColor: getProgressColor(selectedTask.progress) }"></div>
                    </div>
                    <span class="progress-text">{{ selectedTask.progress }}%</span>
                  </div>
                </div>
              </div>
            </div>
            
            <div v-if="selectedTask.status === '已完成'" class="detail-section">
              <h4>执行报告</h4>
              <div class="execution-report">
                <div class="report-item">
                  <label>完成时间</label>
                  <span>{{ selectedTask.completionTime }}</span>
                </div>
                <div class="report-item">
                  <label>执行结果</label>
                  <span class="result-badge success">成功</span>
                </div>
                <div class="report-item full-width">
                  <label>执行总结</label>
                  <p>{{ selectedTask.executionSummary || '无总结报告' }}</p>
                </div>
                <div class="report-actions">
                  <button class="btn-view-report" @click="viewFullReport">查看完整报告</button>
                  <button class="btn-download-report" @click="downloadReport">下载报告</button>
                </div>
              </div>
            </div>
            
            <div v-if="selectedTask.status === '失败'" class="detail-section">
              <h4>失败信息</h4>
              <div class="error-info">
                <div class="error-item">
                  <label>失败时间</label>
                  <span>{{ selectedTask.failureTime }}</span>
                </div>
                <div class="error-item full-width">
                  <label>失败原因</label>
                  <p>{{ selectedTask.failureReason || '未知原因' }}</p>
                </div>
                <button class="btn-retry" @click="retryTask">重试任务</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DroneTaskManagement',
  data() {
    return {
      tasks: [],
      availableFields: [],
      searchQuery: '',
      statusFilter: '',
      typeFilter: '',
      showCreateTaskModal: false,
      showEditTaskModal: false,
      showTaskDetails: false,
      currentTask: {
        id: null,
        name: '',
        type: '',
        fieldId: '',
        fieldName: '',
        scheduledTime: '',
        executionTime: null,
        executor: '',
        status: '待执行',
        description: '',
        urgent: false,
        pesticideType: '',
        usagePerAcre: 0,
        monitorHeight: 0,
        seedType: '',
        seedDensity: 0
      },
      selectedTask: {}
    }
  },
  computed: {
    filteredTasks() {
      return this.tasks.filter(task => {
        const matchesSearch = task.name.toLowerCase().includes(this.searchQuery.toLowerCase()) || 
                            task.fieldName.toLowerCase().includes(this.searchQuery.toLowerCase());
        const matchesStatus = !this.statusFilter || task.status === this.statusFilter;
        const matchesType = !this.typeFilter || task.type === this.typeFilter;
        return matchesSearch && matchesStatus && matchesType;
      });
    },
    pendingTasks() {
      return this.tasks.filter(task => task.status === '待执行').length;
    },
    activeTasks() {
      return this.tasks.filter(task => task.status === '执行中').length;
    },
    completedTasks() {
      return this.tasks.filter(task => task.status === '已完成').length;
    }
  },
  mounted() {
    this.loadTasks();
    this.loadFields();
  },
  methods: {
    // 加载任务数据
    loadTasks() {
      // 模拟从API加载数据
      this.tasks = [
        {
          id: 1,
          name: 'A区作物监测任务',
          type: '作物监测',
          fieldId: 1,
          fieldName: '试验田A区',
          scheduledTime: '2023-10-01T14:30:00',
          executionTime: '2023-10-01T14:30:00',
          executor: '操作员A',
          status: '已完成',
          urgent: false,
          monitorHeight: 30,
          createdAt: '2023-09-30',
          completionTime: '2023-10-01T15:45:00',
          executionDuration: '1小时15分钟',
          coveredArea: 50,
          progress: 100,
          executionSummary: '作物生长状况良好，无病虫害迹象，建议正常灌溉。',
          droneId: 'DR-001'
        },
        {
          id: 2,
          name: 'B区农药喷洒任务',
          type: '农药喷洒',
          fieldId: 2,
          fieldName: '试验田B区',
          scheduledTime: '2024-01-20T09:00:00',
          executionTime: null,
          executor: '',
          status: '待执行',
          urgent: true,
          pesticideType: '广谱杀虫剂',
          usagePerAcre: 0.5,
          createdAt: '2024-01-18',
          progress: 0
        },
        {
          id: 3,
          name: 'C区施肥作业',
          type: '施肥作业',
          fieldId: 3,
          fieldName: '试验田C区',
          scheduledTime: '2024-01-19T10:00:00',
          executionTime: '2024-01-19T10:05:00',
          executor: '操作员B',
          status: '执行中',
          urgent: false,
          usagePerAcre: 2.5,
          createdAt: '2024-01-18',
          executionDuration: '45分钟',
          coveredArea: 30,
          progress: 67,
          droneId: 'DR-002'
        },
        {
          id: 4,
          name: 'D区灌溉监测',
          type: '灌溉监测',
          fieldId: 4,
          fieldName: '示范田D区',
          scheduledTime: '2024-01-18T15:00:00',
          executionTime: '2024-01-18T15:00:00',
          executor: '',
          status: '失败',
          urgent: false,
          createdAt: '2024-01-18',
          failureTime: '2024-01-18T15:15:00',
          failureReason: '无人机传感器故障，无法获取准确数据'
        },
        {
          id: 5,
          name: 'A区播种作业',
          type: '播种作业',
          fieldId: 1,
          fieldName: '试验田A区',
          scheduledTime: '2024-01-22T13:30:00',
          executionTime: null,
          executor: '操作员C',
          status: '待执行',
          urgent: false,
          seedType: '优质水稻种子',
          seedDensity: 30000,
          createdAt: '2024-01-19',
          progress: 0
        }
      ];
    },
    
    // 加载可用农田数据
    loadFields() {
      // 模拟从API加载农田数据
      this.availableFields = [
        { id: 1, name: '试验田A区' },
        { id: 2, name: '试验田B区' },
        { id: 3, name: '试验田C区' },
        { id: 4, name: '示范田D区' }
      ];
    },
    
    // 更新农田名称
    updateFieldName() {
      const field = this.availableFields.find(f => f.id === parseInt(this.currentTask.fieldId));
      if (field) {
        this.currentTask.fieldName = field.name;
      }
    },
    
    // 查看任务详情
    viewTaskDetails(task) {
      this.selectedTask = { ...task };
      this.showTaskDetails = true;
    },
    
    // 编辑任务
    editTask(task) {
      this.currentTask = { ...task };
      this.showEditTaskModal = true;
    },
    
    // 保存任务
    saveTask() {
      const now = new Date().toISOString().split('T')[0];
      
      if (this.showCreateTaskModal) {
        // 创建新任务
        const newTask = {
          ...this.currentTask,
          id: Date.now(),
          status: '待执行',
          createdAt: now,
          progress: 0
        };
        this.tasks.push(newTask);
        alert('任务创建成功');
      } else if (this.showEditTaskModal) {
        // 编辑现有任务
        const index = this.tasks.findIndex(task => task.id === this.currentTask.id);
        if (index > -1) {
          this.tasks[index] = { ...this.tasks[index], ...this.currentTask };
          alert('任务更新成功');
        }
      }
      
      this.closeModal();
    },
    
    // 关闭弹窗
    closeModal() {
      this.showCreateTaskModal = false;
      this.showEditTaskModal = false;
      this.resetCurrentTask();
    },
    
    // 重置当前任务对象
    resetCurrentTask() {
      this.currentTask = {
        id: null,
        name: '',
        type: '',
        fieldId: '',
        fieldName: '',
        scheduledTime: '',
        executor: '',
        description: '',
        urgent: false,
        pesticideType: '',
        usagePerAcre: 0,
        monitorHeight: 0,
        seedType: '',
        seedDensity: 0
      };
    },
    
    // 开始任务
    startTask(taskId) {
      if (confirm('确定要开始执行该任务吗？')) {
        const index = this.tasks.findIndex(task => task.id === taskId);
        if (index > -1) {
          this.tasks[index].status = '执行中';
          this.tasks[index].executionTime = new Date().toISOString();
          this.tasks[index].executor = this.tasks[index].executor || '系统自动';
          alert('任务已开始执行');
        }
      }
    },
    
    // 取消任务
    cancelTask(taskId) {
      if (confirm('确定要取消该任务吗？')) {
        const index = this.tasks.findIndex(task => task.id === taskId);
        if (index > -1) {
          this.tasks[index].status = '已取消';
          alert('任务已取消');
        }
      }
    },
    
    // 删除任务
    deleteTask(taskId) {
      if (confirm('确定要删除该任务吗？')) {
        const index = this.tasks.findIndex(task => task.id === taskId);
        if (index > -1) {
          this.tasks.splice(index, 1);
          alert('任务已删除');
        }
      }
    },
    
    // 重试任务
    retryTask() {
      if (confirm('确定要重试该任务吗？')) {
        const index = this.tasks.findIndex(task => task.id === this.selectedTask.id);
        if (index > -1) {
          this.tasks[index].status = '待执行';
          this.tasks[index].failureTime = null;
          this.tasks[index].failureReason = null;
          this.showTaskDetails = false;
          alert('任务已重置为待执行状态');
        }
      }
    },
    
    // 查看完整报告
    viewFullReport() {
      alert(`查看${this.selectedTask.name}的完整执行报告`);
      // 实际项目中可以跳转到报告详情页
    },
    
    // 下载报告
    downloadReport() {
      alert(`下载${this.selectedTask.name}的执行报告`);
      // 实际项目中可以触发文件下载
    },
    
    // 获取任务类型样式类
    getTaskTypeClass(type) {
      const classMap = {
        '作物监测': 'type-monitor',
        '农药喷洒': 'type-spray',
        '播种作业': 'type-seed',
        '施肥作业': 'type-fertilize',
        '灌溉监测': 'type-irrigation'
      };
      return classMap[type] || 'type-other';
    },
    
    // 获取状态样式类
    getStatusClass(status) {
      const classMap = {
        '待执行': 'status-pending',
        '执行中': 'status-active',
        '已完成': 'status-completed',
        '已取消': 'status-canceled',
        '失败': 'status-failed'
      };
      return classMap[status] || '';
    },
    
    // 获取进度条颜色
    getProgressColor(progress) {
      if (progress === 100) return '#28a745';
      if (progress > 50) return '#17a2b8';
      if (progress > 0) return '#ffc107';
      return '#6c757d';
    },
    
    // 检查是否有任何参数
    hasAnyParams() {
      return this.selectedTask.pesticideType || 
             this.selectedTask.usagePerAcre || 
             this.selectedTask.monitorHeight || 
             this.selectedTask.seedType || 
             this.selectedTask.seedDensity;
    }
  }
}
</script>

